<!--创建公司createcompany -->
<template>
  <div>

    <label class="el-form-item__label" style="width: 120px;">
      公司头像
    </label>
    <!--elementui的上传图片的upload组件-->
    <el-upload
      class="avatar-uploader"
      action=""
      :show-file-list="false"
      :before-upload="beforeupload">
      <img v-if="src" :src="src" class="avatar">
      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    </el-upload>
    <el-form ref="form" :model="form"  :rules="rules"   label-width="120px">
      <el-form-item label="公司名称" prop="company_name">
        <el-input v-model="form.company_name"></el-input>
      </el-form-item>
      <el-form-item label="公司地址" prop="company_address">
        <el-input v-model="form.company_address"></el-input>
      </el-form-item>
      <el-form-item label="公司具体信息" prop="context">
        <el-input
          type="textarea"
          :rows="6"
          placeholder="请输入内容"
          v-model="form.context">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">创建</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState,mapActions } from 'vuex'
  export default {
    data() {
      return {
        form: {
          // user_id:28,
          company_name: '',
          company_address : '',
          context : '',
        },
        rules: {
          company_name: [
            { required: true, message: '请输入公司名称', trigger: 'blur' }
          ],
          company_address: [
            { required: true, message: '请输入公司地址', trigger: 'blur' }
          ],
          context: [
            { required: true, message: '请输入公司具体信息', trigger: 'blur' }
          ],
        },
        param:new FormData(),//表单要提交的参数
        src:"", //展示图片的地址
      };
    },
    methods: {
      ...mapActions(['getuserInfo']),
      //阻止upload的自己上传，进行再操作
      beforeupload(file) {
          //创建临时的路径来展示图片
          var windowURL = window.URL || window.webkitURL;
          this.src=windowURL.createObjectURL(file);
          this.param.append('company_icon', file);
          return false;
      },
      onSubmit(){//表单提交的事件
        this.$refs["form"].validate((valid) => {
          if (valid) {
            if(this.src){
              var form = this.form;
              this.param.append('company_name', form.company_name);
              this.param.append('user_id', this.userInfo.user_id);
              this.param.append('company_address', form.company_address);
              this.param.append('context', form.context);
              url.createcompany(this.param).then(res=>{
                // 创建完成后跳转到我的公司页面，隐藏加入公司和创建公司
                if(res.data.code[0]=='200' || res.data.code[0]=="973"){
                  this.FngetUserInformation()
                  this.$router.push('/UserInformation/myCompany')
                }
                if(res.data.code[0]=='200'){
                  this.$message.success(res.data.message[0]);

                }else{
                  this.$message.success(res.data.message[0]);
                }
              }).catch(err=>{
                console.log(err,'-------创建公司')
              })              
            }else{
              this.$message.error('没有上传头像图片');
            }

          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      FngetUserInformation(){//获取的数据
        url.getUserInformation({
          "user_id":this.userInfo.user_id
        }).then(result=>{ 
          let xx=result.data.data[0].fields
              xx.user_id=result.data.data[0].pk
              this.getuserInfo(xx)
          
        }).catch(err=>{
          console.log(err,'-------tijiaoshibai')
        })
      }
    },
    mounted(){
      // this.form.user_id=localStorage.getItem("user_id")
    },
    computed:{
      ...mapState(['userInfo'])
    }
  }
</script>
<style lang="stylus" scoped>
  >>>.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  >>>.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  >>>.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  >>>.avatar {
    width: 100px;
    height: 100px;
    display: block;
  }
</style>


